<template>
  <div class="createPoster">
    <div class="header">
      <img
        src="//wm-front-common.weimiaocaishang.com/wm-front-landing-page-mobile-custom/img/header.png"
        alt="" />
    </div>
    <div class="form">
      <div class="tips">请输入推广人账号</div>
      <input
        class="name"
        type="text"
        placeholder="请输入推广人账号"
        v-model="form.account" />
      <div class="tips">预览海报</div>
      <img
        class="poster_p"
        src="//wm-front-common.weimiaocaishang.com/wm-front-landing-page-mobile-custom/img/poster4.png"
        alt="" />
    </div>
    <div class="actions">
      <div class="cancel" @click="cancel">取消</div>
      <div class="confirm" @click="confirm">确定</div>
    </div>
    <VanDialog
      class="poster"
      close-on-click-overlay
      theme="round-button"
      :show-confirm-button="false"
      @confirm="download"
      v-model="showPoster">
      <img
        class="poster_img"
        :src="page_image"
        alt="" />
      <div class="content"></div>
    </VanDialog>
  </div>
</template>

<script>
export default {
  name: 'CreatePoster',
  data () {
    return {
      form: {
        account: '',
        qr_url_init: location.origin + '/customer/appid=changqing-shoushen#/SlimCoupon'
      },
      page_image: '',
      showPoster: false
    }
  },
  created () {
    const query = location.search
    this.form.qr_url_init = location.origin + `/customer/${query}&appid=changqing-shoushen#/SlimCoupon`
  },
  mounted () {
    const vh = window.innerHeight * 0.01
    document.documentElement.style.setProperty('--vh', `${vh}px`)
    window.addEventListener('resize', () => {
      const vh = window.innerHeight * 0.01
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    })
  },
  methods: {
    cancel () {
      this.form.account = ''
    },
    async confirm () {
      // 生成海报
      try {
        const data = await this.$apis.userOperator.createPoster(this.form)
        this.page_image = data.page_image
        this.showPoster = true
      } catch (err) {
        if (err.msg) {
          this.$toast({ message: err.msg })
        }
      }
    },
    download () {
      // 下载海报图片
      const a = document.createElement('a')
      const url = this.page_image
      a.setAttribute('href', url + '?response-content-type=application/octet-stream')
      a.setAttribute('download', '')
      a.click()
    }
  }
}
</script>

<style lang="scss" scoped>
.createPoster {
  display: flex;
  flex-direction: column;
  height: 100vh;
  height: calc(var(--vh) * 100);

  /deep/.van-dialog--round-button .van-dialog__footer {
    display: none !important;
  }

  .poster {
    top: 50%;
    border-radius: 20px;

    /deep/.van-dialog__header {
      padding: 0;
      height: 94px;
      line-height: 94px;
    }

    /deep/.van-button__content {
      background: #00c298;
      border-radius: 25px;
    }

    .poster_img {
      width: 100%;
      min-height: 1000px;
    }
  }

  .header {
    min-height: 156px;

    img {
      width: 100%;
    }
  }

  .actions {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 140px;

    .cancel {
      height: 96px;
      text-align: center;
      line-height: 96px;
      width: 208px;
      font-size: 36px;
      font-weight: 400;
      color: #999990;
    }

    .confirm {
      width: 442px;
      height: 96px;
      background: #00c298;
      border-radius: 50px;
      font-size: 36px;
      font-weight: 500;
      color: #fff;
      line-height: 50px;
      text-align: center;
      line-height: 96px;
    }
  }

  .form {
    flex-grow: 1;
    width: 688px;
    height: 868px;
    background: #fff;
    box-shadow: 0 8px 32px 0 rgba(164, 164, 164, 0.15);
    border-radius: 20px;
    position: relative;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: -50px;
    min-height: 800px;

    .name {
      width: 592px;
      box-sizing: border-box;
      height: 100px;
      background: #fafafa;
      border-radius: 16px;
      outline: none;
      border: none;
      font-size: 32px;
      font-weight: 400;
      line-height: 44px;
      padding-left: 2em;
      margin-top: 40px;

      &::placeholder {
        color: #bbb;
      }
    }

    .tips {
      height: 56px;
      font-size: 36px;
      font-weight: 400;
      color: #333;
      line-height: 56px;
      margin-bottom: 20px;
      padding: 30px;
      width: 100%;
      top: 20px;
    }

    .poster_p {
      height: 60%;
      margin-top: 30px;
      background: #e5f7f4;
    }
  }
}
</style>
